export default {
  name: "video_share_dialog",
  props: {
    show: undefined,
    topic_data: undefined
  },
  computed: {
    share_url: function () {
      let share_url = "https://" + this.$web_conf.set_host + "/video/" + this.topic_data.id;
      return share_url
    }
  }
}
